/* default settings */
body, html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.main-menu {
  width: 100%;
  justify-content: space-between;
  background-color: #5e5b5b;
  padding: 1em 2em;
  margin: 0 auto;
  border-top: 3px solid #ebe7e7; }

.nav-menu {
  margin: 0 auto;
  max-width: 1440px;
  justify-content: space-between; }

.link-menu {
  display: inline-block; }

.item-menu {
  list-style: none;
  padding: 0 0.4em;
  font-size: 1.2em;
  cursor: pointer;
  text-decoration: none;
  color: #dadada;
  transition: all 1s ease-in-out; }
  .item-menu:hover {
    font-size: 1.3em;
    border-radius: 5px;
    border-bottom: 3px solid #53c253;
    text-decoration: none;
    color: cyan;
    padding: 0.2em 0.6em; }

.search-menu {
  float: right;
  width: auto;
  margin-right: 2vw; }

.search-menu input {
  margin-right: 1em;
  border-radius: 7px;
  transition: all 1.5s ease-in-out; }
  .search-menu input:hover {
    font-size: 1.15em;
    border-radius: 5px;
    border: 2px solid blue; }

.search-menu button {
  margin-right: 1em;
  border-radius: 7px;
  background-color: #a6ac75;
  transition: all 1s ease-in-out; }
  .search-menu button:hover {
    cursor: pointer;
    font-size: 1.15em;
    border-radius: 5px;
    border: 2px solid blue; }

.main-header {
  background-color: #aaa8a8;
  margin: 0 auto;
  padding: 0.2em 1em; }

.title-header {
  width: 80%;
  padding-left: 5vh;
  color: #fff;
  margin: 0.4em auto;
  max-width: 1280px; }

.slider-header {
  width: 80%;
  height: 25vh;
  margin: 0 auto;
  max-width: 1280px; }

.carousel {
  height: 20vh; }

.slide {
  height: 20vh; }

.carousel-inner {
  height: 20vh;
  border-radius: 15px; }

.carousel-control {
  border-radius: 15px; }

.panel-container {
  width: 76%;
  margin: 0 auto;
  max-width: 1280px;
  display: flex; }

.panel-block {
  flex: 1 1;
  width: 31%;
  background-color: #424242;
  border-radius: 15px;
  padding: 0.5em 1em;
  margin: -2em 0.5em 0 0.5em;
  color: #dadac6;
  font-size: 0.85em; }
  .panel-block button {
    background-color: #1f7888;
    border-radius: 7px; }

.check {
  color: #0ead0e;
  font-weight: 600;
  font-size: 1.1em;
  padding-top: 1em; }

.check:first-child {
  padding-top: 0.2em;
  font-size: 1.4em; }

.check:nth-child(2) {
  padding-top: 0;
  padding-bottom: 1.3em; }

.image-panel {
  width: 20%;
  height: 100%;
  display: inline-flex;
  vertical-align: middle;
  justify-content: center;
  align-items: center; }

.info-panel {
  width: 75%;
  float: right;
  display: inline-grid; }
  .info-panel .header-panel {
    display: block;
    float: right;
    margin: 0.1em 0 0.2em 0; }
  .info-panel .text-panel {
    display: inline-block;
    float: right; }
  .info-panel button {
    float: right;
    margin-right: 1em;
    border-radius: 7px;
    background-color: #288f9c;
    transition: all 1s ease-in-out; }
    .info-panel button:hover {
      cursor: pointer;
      font-size: 1.15em;
      border-radius: 5px;
      border: 3px solid #128626;
      color: #a8ceaf; }

@charset "UTF-8";
.main-content {
  width: 74%;
  margin: 3em auto;
  max-width: 1280px;
  display: flex; }

.text-content {
  width: 70%;
  padding: 1em 0.5em; }
  .text-content h2 {
    font-weight: 600;
    color: #08a308; }
  .text-content ul {
    list-style: none; }
  .text-content ul li::before {
    content: "\2022";
    color: green;
    font-size: 1.4em;
    line-height: 0;
    margin: 0 1em 0.5em 0;
    padding: 0 0 0.5em 0; }

.panels-content {
  width: 30%; }

.join-content {
  background-color: #058621;
  color: #ccc;
  border-radius: 8px;
  height: 2.4em;
  font-size: 1.5em;
  justify-content: center;
  text-align: center;
  vertical-align: center;
  line-height: 2.4em;
  cursor: pointer; }

.news-content-panel {
  background-color: #2867b9;
  border-radius: 10px;
  color: #ccc;
  margin: 1em 0 0 0;
  padding: 1em;
  cursor: pointer; }
  .news-content-panel h3 {
    padding: 0;
    margin: 0 0 0 0.5em; }
  .news-content-panel a {
    color: #ccc;
    display: block;
    font-size: 0.9em;
    margin: 1em 0 0 0; }
    .news-content-panel a span {
      display: inline-block; }
    .news-content-panel a:hover {
      text-decoration: none; }

.footer-main {
  display: block;
  background-color: #656e67;
  justify-content: space-between;
  font-size: 1.1em;
  padding: 0 0.7em;
  margin: 0 auto; }

.owner {
  color: #cabb7d;
  font-weight: 600;
  cursor: pointer; }
  .owner:hover {
    width: auto;
    padding: 0.3em 0.5em;
    border-radius: 15px;
    border: 2px solid green;
    background-color: #4a4a4b;
    transition: all .5s ease-in-out; }

.sub-owner {
  text-decoration: none;
  color: #bbd86c;
  font-weight: 600; }
  .sub-ownerhover a {
    color: #ccc;
    text-decoration: none; }

